import { Component, OnInit, ViewChild } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Http, RequestOptions, URLSearchParams, Response } from '@angular/http';

/**
 * Services
 */

import { MoviesService } from '../../shared/services/movies.service';

/**
 * Models
 */

import { Movie } from '../../shared/models/movies.model';

@Component({
	selector: 'app-movie-search',
	templateUrl: './movie-search.component.html',
	styleUrls: ['./movie-search.component.scss']
})
export class MovieSearchComponent implements OnInit {

	private TOKEN = '8f1fa28d-0f1f-445c-8d2a-c361e5b77af5';

	@ViewChild('searchMovieForm')
	searchMovieForm: NgForm;
	searchError: String;
	searched: Movie;

	constructor(private http: Http, private moviesService: MoviesService) {}

	ngOnInit() {}

	onSubmit() {
		// reset any previous errors
		this.searchError = undefined;
		// set params
		const params = new URLSearchParams();
		params.set('title', this.searchMovieForm.controls.toSearch.value);
		params.set('token', this.TOKEN);
		params.set('limit', '10');
		// initialise request options with params
		const requestOptions = new RequestOptions();
		requestOptions.params = params;
		this.http.get(`http://api.myapifilms.com/imdb/idIMDB`, requestOptions).subscribe( (response: Response) => {
			console.log(response.json());
		}, (error) => {
			console.log(error);
		});
	}

	postMovie() {
		this.moviesService.postMovie(this.searched);
	}
}



