import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Subscription } from 'rxjs/Subscription';
import { Subject } from 'rxjs/Subject';
import * as _ from 'underscore';

/**
 * Models
 */

import { Movie } from '../models/movies.model';

@Injectable()
export class MoviesService {

	movies: Movie[] = [];
	moviesUpdated = new Subject();
	moviesSubscription: Subscription;

	constructor(private http: Http) {
		this.moviesSubscription = this.moviesUpdated.subscribe( (update: Movie[]) => this.movies = update );
		this.getMovies();
	}

	getMovies() {
		this.http.get(`/api/movies/`).subscribe( (response: Response) => {
			this.moviesUpdated.next(<Movie[]>response.json());
		});
	}

	postMovie(movie: Movie) {
		this.http.post(`/api/movies`, movie).subscribe( (response: Response) => {
			this.moviesUpdated.next(this.movies.push(<Movie>response.json()));
		});
	}

	deleteMovie(movieID: String) {
		this.http.delete(`/api/movies/${movieID}`).subscribe( () => {
			this.moviesUpdated.next( _.reject(this.movies, movie => movie._id.toString() === movieID) );
		});
	}
}



